import React, { Component } from 'react';
import { Link } from "react-router-dom"


import "./login.css"


class Login extends Component {
    constructor() {
        super()

        this.state = {

            email: "",
            password: ""
        }
    }

    handleChangeEmail = (event) => {
        this.setState({ email: event.target.value })
        console.log(event.target.value)
        console.log(this.state.email)
    }
    handleChangePass = (event) => {
        this.setState({ password: event.target.value })
        console.log(event.target.value)
        console.log(this.state.password)
    }


    handlesubmit = (event) => {
        console.log(event)
        console.log(this.state)
        event.preventDefault();
        fetch("http://localhost:7000/user/login", {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(this.state)
        })
            .then((Response) => Response.json())
            .then((result) => {
                console.log(result)
                if (result.auth === false) { alert(result.message); }

                else {
                    sessionStorage.setItem('_ltk', result.token);
                    sessionStorage.setItem('name', result.name);
                    sessionStorage.setItem('email', result.email);
                    sessionStorage.setItem('id', result.id);
                    this.props.history.push('/')
                }
            })
    }

    render() {
        return (
            <div style={{backgroundColor: "rgb(250, 251, 252)"}}>
                <div className="head"><h1 style={{fontWeight:"700"}}>Image Viewer</h1>
                </div>
               
                <div className="hmain">
                    <div ><div>Log in to continue to:</div><div style={{fontWeight:"bold"}}>Image Viewer</div></div>
                    <form onSubmit={this.handlesubmit}>
                    <div>
                        <input onChange={this.handleChangeEmail} type="email" placeholder="Enter email"/>
                        <input onChange={this.handleChangePass} type="password" placeholder="Enter password"/>
                        <button type="submit" style={{color:"white",backgroundColor:"blue",cursor:"pointer"}}>Log in</button>
                    </div>
                    </form>
                    
                <div ><ul style={{display:"flex",listStyleType:"none"}}><li><a  href="#"><span>Can't log in?</span></a></li><p style={{marginLeft:'5px',marginRight:"5px"}}>•</p><li><Link to="/register"><span>Sign up for an account</span></Link></li></ul></div>
                </div>
                
            </div>
          
        );
    }
}

export default Login;


