import React, { Component } from 'react';
import { Link } from "react-router-dom"


import { createStyles, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
//import Link from '@material-ui/core/Link';
import "./headerr.css"







class Headerr extends Component {
  constructor() {
    super()

    this.state = {
    }
  }


  handleLogout = (e) => {
    e.preventDefault()
    sessionStorage.clear();
    this.props.history.push('/')
  }

  clearit = (e) =>{
    e.preventDefault()
    sessionStorage.removeItem("todoeinvitt");
    this.props.history.push('/dashboard')
  }



  render() {

    return (
      <div>
        <div id="stand">
          <AppBar position="fixed" color="primary" style={{ backgroundColor: "rgba(0,0,0, 0.3)" }}>
            <Toolbar >
              <div style={{display:"flex",flexDirection:"row"}}>
                <Typography  variant="h6" >
                  ImageViewer
                  </Typography>
                </div>
              <div style={{ marginTop: "1vh" }}>
                <Typography variant="h8" style={{ fontSize: "0.923vw" }} >
                 
                </Typography>
              </div>
              <div>
                <Typography>
                  {
                    sessionStorage.getItem("_ltk")
                      ? <div class="dropdown">
                        <button class="dropbtn" >{sessionStorage.getItem("name")}</button>
                        <div class="dropdown-content">
                          <Link to="/profile">MyProfile</Link>
                          <Link onClick={this.handleLogout}>Logout</Link>

                        </div>
                      </div>

                      :
                      <div className="dm">
                        <Link to="/login" style={{
                          color: "white", marginRight: "15px", border: "1px solid white", borderRadius: "5px",
                          padding: "8px 15px"
                        }} >Log In</Link>
                        <Link to="/register" style={{
                          color: "white", border: "1px solid white", borderRadius: "5px",
                          padding: "8px 15px"
                        }} >Sign up</Link>
                      </div>
                  }
                </Typography>
              </div>
            </Toolbar>
          </AppBar>
        </div>


      </div>
    );
  }

}

export default Headerr;



