import React,{Component} from 'react';
import {connect} from 'react-redux';
import MyclickDisplay from '../component/myclickDisplay';
import {IndiaLatestVideo} from '../actions/actionfile'

class Myclick extends Component{
    componentDidMount(){
        this.props.dispatch(IndiaLatestVideo())
    }

    render(){
        console.log("<<<<>>>>",this.props.IvideosList)
        // const {pro} = this.props.product.detailProduct
        return(
           
                    <div>
                        <h2>MY Images</h2>
                       <MyclickDisplay  tvideos={this.props.IvideosList}/>
                    </div>
                )
    }
    }


function mapStateToProps(state){
    return{
        IvideosList:state.Hvideos
    }
}


export default connect(mapStateToProps)(Myclick);