
import { Link } from "react-router-dom"
import "./wclickDisplay.css"
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import React, { useState, useEffect } from 'react';




function getModalStyle() {
        const top = 50
        const left = 50

        return {
                top: `${top}%`,
                left: `${left}%`,
                transform: `translate(-${top}%, -${left}%)`,
        };
}

const useStyles = makeStyles((theme) => ({
        paper: {
                position: 'absolute',
                width: 1000,
                backgroundColor: theme.palette.background.paper,
                border: '1px solid #000',
                boxShadow: theme.shadows[5],
                padding: theme.spacing(2, 4, 3),
        },
}));



const WclickDisplay = (props) => {
        const classes = useStyles();
        // getModalStyle is not a pure function, we roll the style only on the first render
        const [modalStyle] = React.useState(getModalStyle);
        const [open, setOpen] = React.useState(false);
        const [item1, setItem1] = React.useState([]);
        const [file, setFile] = React.useState([]);
        const [title, setTitle] = React.useState(null);
        const [author, setAuthor] = React.useState(null);
        const [des, setDes] = React.useState(null);
        const formData = new FormData()

        const handleOpen = (x) => {
                setOpen(true);
                setItem1(x)
                console.log(x)

        };

        const handleClose = () => {
                setOpen(false);
        };

        const handleChangefile = (e) => {
                // const files = e.target.files
                // // const formData = new FormData()
                // formData.append('file', files[0])

                e.preventDefault()
                console.log(e)
                console.log(e.target.files)
                console.log(e.target.files[0])
                setFile(e.target.files[0])


        }

        const handleChangetitle = (e) => {
                e.preventDefault()
                setTitle(e.target.value)
        }

        const handleChangeauthor = (e) => {
                e.preventDefault()
                setAuthor(e.target.value)
        }

        const handleChangedes = (e) => {
                e.preventDefault()
                setDes(e.target.value)
        }

        const body = (x) => {
                console.log(x)
                return (
                        <div style={modalStyle} className={classes.paper}>

                                <img style={{ width: "24.5cm", height: "15cm" }} src={x.image} alt="" />
                                <div style={{ position: "absolute", bottom: "23px", border: "1px solid gray", width: "24.5cm", height: "2cm", backgroundColor: "black", opacity: ".7", color: "white" }}>
                                        <h5 style={{ color: "white", zIndex: "10", marginTop: "5px" }}>{x.title}</h5>
                                        <p>{x.description}</p>
                                </div>


                        </div>
                )
        };

        const renderView = ({ tvideos }) => {

                if (tvideos.indaitrendingVideo) {
                        return tvideos.indaitrendingVideo.map((item, idx) => {
                                return (
                                        <div>

                                                <img onClick={() => handleOpen(item)} className="wdisplay" src={item.image} alt="img" />
                                                <Modal
                                                        open={open}
                                                        onClose={handleClose}
                                                        aria-labelledby="simple-modal-title"
                                                        aria-describedby="simple-modal-description"
                                                >
                                                        {body(item1)}
                                                </Modal>
                                        </div>
                                )
                        })
                }
        }

        const  handlesubmit = (event) => {
                
                event.preventDefault()
                const user ={
                        title: title,
        description: des,
        author:author,
        file:file
                }
                console.log(file)
                console.log("dasdasd",user)
                event.preventDefault();
                fetch("http://localhost:7000/image/addImage",{
                    method:'POST',
                    headers:{
                        'Accept':'application/json',
                        'Content-Type':'application/json'
                    },
                    body:JSON.stringify(user)
                //     body: formData
                //     body: formData
                //     body:JSON.stringify(user),
                //     file:JSON.stringify(file)
                })
                .then((Response) => Response.json())
            .then((result) => {
                console.log(result)
               
                })
            }

        return (
                <div style={{ display: "flex", flexWrap: "wrap" }}>
                        {renderView(props)}
                        {/* <form onSubmit={ handlesubmit } className="centry" enctype="multipart/form-data">

                                <input onChange={handleChangefile} name="file" type="file" />
                                <input onChange={handleChangetitle} style={{ outline: "none" }} type="text" placeholder="Title" />
                                <input onChange={handleChangeauthor} style={{ outline: "none" }} type="text" placeholder="author" />
                                <textarea onChange={handleChangedes} style={{ outline: "none" }} type="text" placeholder="description" />
                                <button type="submit">upload</button>
                        </form> */}

                </div>
        )
}


export default WclickDisplay;



